@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/open-sans-light.woff2") format("woff2"), url("../fonts/open-sans-light.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/open-sans-semibold.woff2") format("woff2"), url("../fonts/open-sans-semibold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: url("../fonts/open-sans-bold.woff2") format("woff2"), url("../fonts/open-sans-bold.woff") format("woff"); }

.body {
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  position: relative;
  margin: 0 auto;
  color: #000000;
  font-family: "Open Sans", "Arial", sans-serif;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

.body,
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.contacts__text {
  font-size: 17px;
  line-height: 31px;
  font-weight: 400;
  text-align: center;
  color: rgba(0, 0, 0, 0.7); }

.contacts__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.contacts__link {
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  display: block;
  text-decoration: none;
  color: inherit;
  text-align: center;
  text-align: left;
  color: rgba(0, 0, 0, 0.5); }
  .contacts__link:hover {
    color: rgba(0, 0, 0, 0.8);
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out; }

.facts__list {
  list-style: none;
  margin: 0;
  padding: 0;
  counter-reset: myCounter; }

.facts__item {
  font-size: 22px;
  line-height: 34px;
  font-weight: 400;
  position: relative;
  text-align: center;
  letter-spacing: -0.5px; }
  .facts__item::before {
    font-size: 28px;
    line-height: 38px;
    font-weight: 700;
    position: absolute;
    counter-increment: myCounter;
    content: "0" counter(myCounter);
    top: 0;
    left: 50%;
    right: 50%;
    color: #0ad9c6;
    text-align: center;
    letter-spacing: -1.27273px; }

.form__text {
  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  text-align: center;
  letter-spacing: -0.005em; }

.form__input {
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #1c1c1c; }
  .form__input::-webkit-input-placeholder {
    opacity: 0.5; }
  .form__input::-moz-placeholder {
    opacity: 0.5; }
  .form__input:-ms-input-placeholder {
    opacity: 0.5; }
  .form__input::-ms-input-placeholder {
    opacity: 0.5; }
  .form__input::placeholder {
    opacity: 0.5; }

.form__submit {
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  padding: 15px 20px;
  border: none;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #0ad9c6;
  cursor: pointer;
  background-color: #0ad9c6;
  border-radius: 6px; }
  .form__submit:hover {
    background-color: rgba(10, 217, 198, 0.8); }

.header {
  color: #ffffff;
  background-image: url("../img/header-desk.jpg"); }

.header__title {
  font-size: 46px;
  line-height: 64px;
  font-weight: 700; }

.header__text {
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
  opacity: 0.8; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.nav__link {
  display: block;
  text-decoration: none;
  color: inherit;
  text-align: center;
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  opacity: 0.7; }
  .nav__link:hover {
    opacity: 1;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out; }

.nav__burger {
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  padding: 15px 20px;
  border: none;
  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #0ad9c6;
  cursor: pointer; }

.section-title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  color: #1f1f1f;
  text-align: center;
  letter-spacing: -0.005em; }
  .section-title--white {
    color: #ffffff; }

.types {
  background-color: #f6f7f9; }

.types__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.types__description {
  font-size: 22px;
  line-height: 34px;
  font-weight: 600;
  text-align: center;
  letter-spacing: -0.5px; }

.video {
  background-color: #131314; }

.video__text {
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
  letter-spacing: -0.005em;
  opacity: 0.7; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }
